body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #1a1a1a;
    color: #fff;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.title {
    font-size: 24px;
    font-weight: bold;
}

.filter-container {
    background-color: #2a2a2a;
    border-radius: 4px;
    padding: 16px;
    margin-bottom: 20px;
}

.weapon-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 16px;
}

.weapon-card {
    background-color: #2a2a2a;
    border-radius: 4px;
    overflow: hidden;
    transition: transform 0.3s;
}

.weapon-card:hover {
    transform: translateY(-5px);
}

.weapon-image {
    width: 100%;
    height: 150px;
    object-fit: contain;
    background-color: #3a3a3a;
    padding: 10px;
}

.weapon-info {
    padding: 12px;
}

.weapon-name {
    font-weight: bold;
    margin-bottom: 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.weapon-type {
    color: #999;
    font-size: 12px;
}

.rarity-covert {
    color: #eb4b4b;
}

.rarity-classified {
    color: #d32ce6;
}

.rarity-restricted {
    color: #8847ff;
}

.rarity-milspec {
    color: #4b69ff;
}

.rarity-industrial {
    color: #5e98d9;
}

.rarity-consumer {
    color: #b0c3d9;
}